@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap");

*{
    box-sizing: border-box;
}



html, body, #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
     background: #ffffff;
  overflow: hidden;
    
}

body {
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    }


canvas {
  position: absolute;
}

.content { 
    white-space: nowrap;
    border-style: solid;
    border-width: 10px;

    border-color: rgb(255, 162, 0);
  padding-top: 10px;
  transform: translate3d(50%, 0, 0);
  text-align: left;
  background: #202035;
  background: none;
  color: black;
  padding: 10px 15px;
  
}




.row-col{
    text-align: left;
    color: rgb(155, 0, 0);

}